import React from 'react';
import { connect } from 'react-redux';
import {BrowserRouter as Router,Switch,Route, Link} from "react-router-dom";
import Inicio from './pages/inicio';
import Booking from './pages/booking';
import Leisure from './pages/leisure';
import OtherService from './pages/other_services';
import Profile from './pages/profile';
import RoomService from './pages/room_service';
import Transport from './pages/transport';
import Navbar from './components/navbar.js';
import NavbarInicio from './components/navbarInicio.js';
import Diamond from './components/diamond.js';
import default_profile from './media/default_profile.jpeg';
import './public/navbar.css';


function App(props) {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div>
        <Navbar/>
        <NavbarInicio/>  
        <Switch>
          <Route exact path="/">
            <Inicio/>
          </Route>
          <Route path="/leisure">
            <Leisure/>
          </Route>
          <Route path="/booking">
            <Booking/>
          </Route>
          <Route path="/other_services">
            <OtherService/>
          </Route>
          <Route path="/profile">
            <Profile/>
          </Route>
          <Route path="/room_service">
            <RoomService/>
          </Route>
          <Route path="/transport">
            <Transport/>
          </Route>
        </Switch>
      </div>

    </Router>
  );
}
    
   
  

function mapStateToProps(state) {
return {
...state
};
}
export default connect(mapStateToProps)(App);
